.profile__name {
    font-family: 'Inter', 'Arial', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    max-width: 500px;
    padding: 0 18px 0 0;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #ffffff;

}

@media screen and (max-width: 657px) {
    .profile__name {
        max-width: 320px;
        font-size: 27px;
        line-height: 33px;
        padding: 0 10px 14px 0;
    }
}

@media screen and (max-width: 433px) {
    .profile__name {
        max-width: 200px;
    }
}

